.Header {
  background-color: #61dafb;
  display: flex;
  justify-content: flex-end;
  margin: 30px;
}

.Adaptable{
  background-color: #61fbda;
}

.Friends{
  display: flex;
  flex-wrap: wrap;
}

.Friends > div{
  width: 100px;
  margin: 10px;
  text-align: center;
  line-height: 75px;
  font-size: 30px;
}

.Menu {
  width: 500;
  justify-content: center;
  display: flex;
}

.Menu_button {
  height: 3em;
  padding: 1.5em;
  will-change: filter;
}

.menu_button:hover { /*permet un ombrage quand point√© a reutiliser pour le menu :3*/
  filter: drop-shadow(0 0 2em #646cffaa);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
